import { Link, useLoaderData } from "remix";
import { getArt } from "~/services/art";

export let loader = async ({params}) => {
  
  return {
    params,
    data: await getArt(params.id)

  
  }
};

export default function Index() {
    const {params,data} = useLoaderData();
  return (
    <div>
      <Link to="/art" >Voltar</Link >
      {params.id}

      <img src={data?.thumbnail?.lqip}></img>
      <span>{data?.name}</span>
    </div>
  );
}
